import { instance } from './axios';
import { AxiosRequestConfig, AxiosResponse } from 'axios';
import { HttpRes, DelayResult, LrcTime } from '@/interfaces';

export * from './localStorage';

/**
 *  index  keyparam1-param2- ...
 * @param args 
 */
export function generateKey(...args: Array<string | number>) {
  return args.join('-');
}

/**
 * 00:00:00 00:
 * @param second 
 * @param isMilli 
 */
export function formatSecond(second: number, isMilli: boolean = false) {
  const t = Math.floor(isMilli ? second / 1000 : second);
  const hour = Math.floor(t / 3600);
  const min = Math.floor(t % 3600 / 60);
  const sec = t % 60;

  return [hour, min, sec]
    .filter((val, index) => index !== 0 || val > 0)
    .map((val) => `00${val}`.slice(-2))
    .join(':');
}

/**
 * 
 * @param str 
 */
export function format2millisecond(str: string) {
  return str
    .trim()
    .split(':')
    .map((s) => +s)
    .reverse()
    .reduce((pre, next) => pre + next * 60) * 1000;
}

/**
 *  axios 
 * @param p axios request response
 */
const dealAxiosResult = (p: Promise<AxiosResponse>): Promise<HttpRes> => p.then((res: AxiosResponse) => {
  if (res.data.code === 200) {
    return res.data || {};
  } else {
    return {
      ...res.data,
      failMark: true,
    };
  }
}).catch((err) => ({
  failMark: true,
  httpError: err,
}));

/**
 * axios post method
 * @param url url
 * @param data payload data
 * @param config axios config
 */
export function post(url: string, data?: any, config?: AxiosRequestConfig): Promise<HttpRes> {
  return dealAxiosResult(instance.post(url, data, config));
}

/**
 * axios get method
 * @param url url
 * @param config axios config
 */
export function get(url: string, config?: AxiosRequestConfig): Promise<HttpRes> {
  return dealAxiosResult(instance.get(url, config));
}

/**
 * 
 * @param obj 
 * @param prop 
 * @param defaultVal 
 */
export function getProp(obj: any, prop: string, defaultVal: any): any {
  if (obj && obj[prop]) {
    return obj[prop];
  } else {
    return defaultVal;
  }
}

/**
 * 
 * @param left 1
 * @param right 2
 */
export function withinErrorMargin(left: number, right: number): boolean {
  return Math.abs(left - right) < Number.EPSILON * Math.pow(2, 2);
}

export interface ControlScrollParam {
  el: HTMLElement;
  duration: number;
  y: number;
  endCall?: () => void;
}

/**
 * (y)
 * @param el html 
 * @param duration 
 * @param y 
 */
export function controlScroll(param: ControlScrollParam): () => void {
  const {
    el,
    duration,
    y,
    endCall = () => void 0,
  } = param;

  let {scrollTop} = el;
  const deltY = y - scrollTop;

  let start = 0;
  let lastTime = 0;
  let canContinue = true;

  const loop = (now: number) => {
    if (lastTime === 0) {
      start = now;
      lastTime = now;
    }
    const deltTime = now - lastTime;
    const perMoveY = withinErrorMargin(deltTime, 0) ? 0 : deltY / (duration / 1000) * (deltTime / 1000);
    scrollTop += perMoveY;

    el.scrollTo({
      left: 0,
      top: scrollTop,
      behavior: 'instant',
    });

    if (
      Math.abs(el.scrollTop - y) >= 6
      //  50ms
      && now - start <= duration + 50
      && canContinue
    ) {
      lastTime = now;
      window.requestAnimationFrame(loop);
    } else {
      // end or cancel
      endCall();
    }
  };

  window.requestAnimationFrame(loop);

  return function clear() {
    canContinue =  false;
  };
}

/**
 * promise  setTimeoutdelay(...).promise.then(...)
 * @param func 
 * @param millisecond 
 * @param options 
 */
export function delay(func: (...args: any[]) => any, millisecond: number, options?: any): DelayResult {
  let timer = 0;
  let reject: any = null;
  const promise = new Promise((resolve, tmpReject) => {
    reject = tmpReject;
    timer = window.setTimeout(() => {
      resolve(func(options));
    }, millisecond);
  });

  return {
    get promise() {
      return promise;
    },
    cancel() {
      if (timer) {
        clearTimeout(timer);
        timer = 0;
        reject(new Error('timer is cancelled'));
        reject = null;
      }
    },
  };
}

/**
 * 
 * @param lyric 
 */
export function getLyricArr(lyric: string | undefined): LrcTime[] {
  const result: LrcTime[] = [];

  (lyric || '').split(/[\n\r]/).map((str) => {
    const reg = /\[(\d+:\d+\.?\d+)\]/g;
    const content = str.trim().replace(reg, '');
    let res: null | string[] = null;

    while (true) {
      res = reg.exec(str.trim());
      if (!res) {
        break;
      }

      result.push({
        formatTime: res[1],
        millisecond: format2millisecond(res[1]),
        content,
      });
    }
  });

  return result.sort((a, b) => a.millisecond - b.millisecond);
}
